td {
            border-bottom: 1px solid #ccc;
            height: 33px;
            font-size: 12px;
            text-transform: uppercase;
            font-weight: 600;
            min-width: 50px;
        }

        * {
            color: black;
            font-family: 'Montserrat', sans-serif;
            transition: 0.5s;
        }
		
		body {
			background-color: white;
		}

        .border {
            border: 1px solid green;
            min-height: 500px;
            height: 500px;
        }

        .l {
            text-align: center;
        }

        .n {
            text-align: center;
            border-bottom: 1px solid #ccc;
            padding: 5px;
        }

        .r {
            text-align: left;
            min-width: 80px;
            border-bottom: 1px solid #ccc;
            padding: 5px;
            border-left: 1px dashed #ccc;
        }

        .name {
            text-align: left;
        }

        .teacher {
            font-size: 10px;
        }

        .name {
            border-left: 1px dashed #ccc;
            padding: 5px;
        }

        .time {
            border-left: 1px dashed #ccc;
            padding: 5px;
        }

        .day {
            min-width: 350px;
            width: 350px;
            color: white;
        }

        .nameday {
            background-color: green;
            height: 50px;
        }

        table:hover {
            border: 1px solid blue;
        }

        table:hover .nameday {
            background-color: blue;
            height: 50px;
        }

        .table {
            display: inline-block;
            min-width: 350px;
            width: 350px;
            min-height: 500px;
            height: 500px;
            margin: 20px;
        }